import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import { FieldTip, LinkBlank } from '@mssgme/ui';

import styles from './Domain.scss';

export const DnsRecord = ({ record: { host, value: cname } = {} }) => {
    const { t } = useTranslation('landings');

    return (
        <FieldTip title={t('Website.Edit.Domain.tips.new.title')} className={styles.fieldTip}>
            <p>{t('Website.Edit.Domain.tips.new.messages.1')}</p>
            <ol className={styles.list}>
                <li>{t('Website.Edit.Domain.tips.new.messages.list.1')}</li>
                <li>{t('Website.Edit.Domain.tips.new.messages.list.2')}</li>
                <li
                    dangerouslySetInnerHTML={{
                        __html: t('Website.Edit.Domain.tips.new.messages.list.3', { host, cname }),
                    }}
                />
                <li
                    dangerouslySetInnerHTML={{
                        __html: t('Website.Edit.Domain.tips.new.messages.list.4', { host }),
                    }}
                />
            </ol>
            <p style={{ margin: 0 }}>{t('Website.Edit.Domain.tips.new.messages.ps')}</p>
            <p style={{ margin: '12px 0 0 0' }}>
                <LinkBlank href="https://mssg.me/faq-next#q3-3">
                    {t('Website.Edit.Domain.tips.new.messages.faq')}
                </LinkBlank>
            </p>
        </FieldTip>
    );
};

DnsRecord.propTypes = {
    record: PropTypes.shape({
        host: PropTypes.string,
        value: PropTypes.string,
    }),
};
