// todo: update react-i18next and test if native Suspense support is fixed
export const useTranslationsSuspense = ({ ready, i18n }) => {
    if (ready) {
        return;
    }

    throw new Promise((resolve, reject) => {
        const off = () => {
            i18n.off('loaded', loaded);
            i18n.off('failedLoading', failedLoading);
        };
        const loaded = () => {
            off();
            resolve();
        };
        const failedLoading = (lng, ns, msg) => {
            off();
            reject(new Error(`Failed to load localization "${ns}:${lng}" (${msg}`));
        };

        i18n.on('loaded', loaded);
        i18n.on('failedLoading', failedLoading);
    });
};
