import React from 'react';
import { Route, Switch } from 'react-router-dom';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { AnimateableSwitch } from '@mssgme/ui';
import { LandingsContextsProvider } from '../UI';
import { PageDelete, PageHome, PageNameEdit, PageSettings, PageSetup, PageSlugEdit, PageStatusEdit } from './Page';
import { VisualBuilder } from './VisualBuilder';

const routing = {
    settings: {
        component: PageSettings,
        children: {
            slug: PageSlugEdit,
            name: PageNameEdit,
            status: PageStatusEdit,
            delete: PageDelete,
        },
    },
};

export const LandingsPageRouter = () => {
    const { website, page } = useWebsitePageBySlugs();

    if (!page) {
        return null;
    }

    return (
        <LandingsContextsProvider base={`/pages/-/${website._id}/${page._id}`} website={website} page={page}>
            <Switch>
                <Route path="/pages/-/:websiteSlug([a-f\d]{24})/:pageSlug([a-f\d]{24})/setup" component={PageSetup} />
                <Route path="/pages/-/:websiteSlug([a-f\d]{24})/:pageSlug([a-f\d]{24})/edit" component={VisualBuilder} />
                <Route path="/pages/-/:websiteSlug([a-f\d]{24})/:pageSlug([a-f\d]{24})">
                    <PageHome />
                    <AnimateableSwitch routing={routing} />
                </Route>
            </Switch>
        </LandingsContextsProvider>
    );
};
