import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import { useTranslation } from 'react-i18next';
import { FiCheck, FiCopy } from 'react-icons/fi';

import { prettifyUrl } from '@mssgme/shared';
import { useCopyText } from '@mssgme/hooks';
import { LandingsPropTypes, LinkBlank } from '@mssgme/ui';

import styles from './CopyableWebsiteLink.scss';

export function CopyableWebsiteLink({ url = '', textToCopy, website, page, ...rest }) {
    const { t } = useTranslation('landings');
    const { isCopied, copyText } = useCopyText();
    const isPublished = page.enabled;
    const Tag = isPublished ? LinkBlank : 'div';
    const attributes = isPublished && { href: url };

    return (
        <div className={cn({ [styles.root]: true, [styles.notPublished]: !isPublished })} {...rest}>
            <Tag
                className={styles.personalLink}
                {...attributes}
            >
                {isPublished ? prettifyUrl(url) : t('Website.View.Home.not_published')}
            </Tag>

            {textToCopy && website.enabled && (
                <div
                    style={{ pointerEvents: isCopied ? 'none' : 'auto' }}
                    className={cn({
                        [styles.copyButton]: true,
                        [styles.copied]: isCopied,
                    })}
                    onClick={() => copyText(textToCopy)}
                >
                    <FiCopy size={20}/>
                    <FiCheck size={20}/>
                </div>
            )}
        </div>
    );
}

CopyableWebsiteLink.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
    url: PropTypes.string,
    textToCopy: PropTypes.string,
};
