import React from 'react';
import { useParams } from 'react-router-dom';
import { useNavigationUtils, useTrack } from '@mssgme/hooks';
import { SecondLevelScreen } from '@mssgme/ui';

import { SinglePlanPageContent } from '../UI';

export default function SinglePlanPage() {
    const { generateUrl } = useNavigationUtils();
    const { plan: planId } = useParams();

    useTrack('visit-plan-page', { planId });

    return (
        <SecondLevelScreen backPath={generateUrl('../..')} preferDynamicBack light>
            <SinglePlanPageContent />
        </SecondLevelScreen>
    );
}
