import cn from 'classnames';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { Redirect } from 'react-router-dom';
import { FaUserTag } from 'react-icons/fa';

import { useBilling, useNavigationUtils, useUpgradeLocation, useWebsitePageBySlugs } from '@mssgme/hooks';
import { LinkButton, Nav, NavItemLink, PersonalLinkNav, RestrictionScope, SecondLevelScreen } from '@mssgme/ui';
import { Edit2Icon, PieChartIcon, SettingsIcon, UsersIcon } from '@mssgme/icons';
import { ProductsIcon } from '@mssgme/products';
import { useSingleWebsiteFeedbackCounts, useWebsiteUrl } from '../../../../hooks';
import { BanBanner, CopyableWebsiteLink, FeedbacksBadge, PublishButton } from '../../../../UI';

import styles from './WebsiteHome.scss';

export function WebsiteHome() {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const { isFree } = useBilling();
    const feedbacks = useSingleWebsiteFeedbackCounts(website._id);
    const upgradeUrl = useUpgradeLocation();
    const websiteUrl = useWebsiteUrl(website);

    if (!page) {
        return <Redirect to={generateUrl('..')} />;
    }

    const topContent = (
        <>
            <CopyableWebsiteLink
                url={websiteUrl}
                // textToCopy={absoluteUrl}
                website={website}
                page={page}
            />
            {website.enabled && (
                <PersonalLinkNav
                    className={styles.personalLink}
                    // embedTo={generateUrl('share/embed')}
                    sharePageTo={generateUrl('share')}
                    qrPageTo={{ pathname: generateUrl('standalone/qr'), state: { backPath: '../..' } }}
                    textToCopy={websiteUrl}
                    website={website}
                />
            )}
        </>
    );

    return (
        <SecondLevelScreen
            title={website.name}
            backPath={generateUrl('../..')}
            titleRightContent={
                <>
                    <PublishButton
                        className={styles.topButton}
                        btnStyle="primary"
                        website={website}
                        page={page}
                        publishedUrl="publish/published"
                        data-button="publish"
                    >
                        {t('Editor.Dashboard.header.publish')}
                    </PublishButton>

                    {website.enabled && isFree && (
                        <LinkButton
                            className={cn(styles.upgradeButton, styles.upgradeButton)}
                            outline
                            btnStyle="primary"
                            to={upgradeUrl}
                            data-button="website-home-upgrade-button"
                            data-event="click-upgrade"
                            data-variant="button"
                        >
                            {t('common:misc.upgrade')}
                        </LinkButton>
                    )}
                </>
            }
            topPanelChildren={topContent}
        >
            <RestrictionScope>
                <Nav>
                    <NavItemLink to={generateUrl('edit')} icon={Edit2Icon}>
                        {t('Website.View.Home.items.editor')}
                    </NavItemLink>
                    <NavItemLink to={generateUrl('audience')} icon={UsersIcon}>
                        {t('Website.View.Home.items.audience')}
                    </NavItemLink>
                    <NavItemLink to={generateUrl('analytics')} icon={PieChartIcon}>
                        {t('Website.View.Home.items.analytics')}
                    </NavItemLink>
                    <NavItemLink
                        to={generateUrl('feedback')}
                        icon={FaUserTag}
                        chevron={<FeedbacksBadge {...feedbacks} />}
                    >
                        {t('Website.View.Home.items.feedback')}
                    </NavItemLink>
                    <NavItemLink to={generateUrl('products')} icon={ProductsIcon}>
                        {t('Website.View.Home.items.products')}
                    </NavItemLink>
                    <NavItemLink to={generateUrl('settings')} icon={SettingsIcon}>
                        {t('Website.View.Home.items.settings')}
                    </NavItemLink>
                </Nav>
            </RestrictionScope>

            {website.moderation?.blocked && <BanBanner moderation={website.moderation} />}
        </SecondLevelScreen>
    );
}
