import React from 'react';

import { AnimateableSwitch } from '@mssgme/ui';
import { PageProductsRouting } from '@mssgme/userpage-core';

const router = {
    component: PageProductsRouting,
    exact: false,
    props: { absolute: true },
};

const routing = {
    'add-block/:kind': router,
    'block/:kind/:blockId': router,
    'preview/:previewDevice(desktop|mobile)': router,
};

export const LandingsEditorProductsRouter = () => <AnimateableSwitch routing={routing} />;
