import React from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import styles from './PastDueBanner.scss';

export default function PastDueBanner({ plan }) {
    const { t } = useTranslation('ui');

    return (
        <div className={styles.root}>
            <Link to="/billing" className={styles.link} data-event="click-past-due" data-variant="top-banner">
                <div
                    className={styles.message}
                    dangerouslySetInnerHTML={{ __html: t('PastDueBanner.message', { plan }) }}
                />
                <div className={styles.button}>{t('PastDueBanner.button')}</div>
            </Link>
        </div>
    );
}

PastDueBanner.propTypes = {
    plan: PropTypes.string.isRequired,
};
