import React from 'react';
import PropTypes from 'prop-types';
import { Route } from 'react-router-dom';

import { MssgmeWidget } from '@mssgme/ui';

const help_ids = {
    ru: '6059b4c0c69fd700180c18f6/6059b4c0c69fd700180c18fb',
    uk: '625e915b62aa81001f023e6d/625e915b62aa81001f023e6e',
    en: '6059bde2c69fd700180c1ef6/6059bde2c69fd700180c1efb',
};

export const HelpWidget = ({ lang }) => {
    const helpId = help_ids[lang];

    if (!helpId || process.env.NODE_ENV !== 'production') {
        return null;
    }

    return (
        <Route exact path={['/pages/-/:websiteSlug/:pageSlug?/edit?']}>
            <MssgmeWidget src={`https://mssg.me/widget/${helpId}`} />
        </Route>
    );
};

HelpWidget.propTypes = {
    lang: PropTypes.string.isRequired,
};
