import React from 'react';
import { Redirect, useParams } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import moment from 'moment';

import { useBilling, useNavigationUtils } from '@mssgme/hooks';
import { SecondLevelScreen } from '@mssgme/ui';

import { Checkout } from '../UI';
import { useFormatPrice } from '../hooks';
import Logo from '../../icons/mssgme_logo_rock.svg';

import styles from './ConfirmSubscriptionPage.scss';

export default function ConfirmSubscriptionPage() {
    const { t } = useTranslation('billing');
    const { generateUrl } = useNavigationUtils();
    const { plan: planId, period } = useParams();
    const { plan, allowPay } = useSelector(({ billingDetails: { plans, result } }) => {
        return {
            plan: plans?.find((plan) => plan.id === planId),
            allowPay: !result?.subscription,
        };
    });
    const price = useSelector((state) => state.checkout.price ?? plan?.prices?.[period]);
    const formatPrice = useFormatPrice();
    const { isTrialAvailable } = useBilling();

    const redirectUrl = generateUrl('../..');

    if (!plan) return <Redirect to={redirectUrl} />;
    if (!allowPay) return <Redirect to={redirectUrl} />;

    return (
        <SecondLevelScreen
            light
            preferDynamicBack
            backPath={{
                pathname: redirectUrl,
                state: { preventDynamicBack: true },
            }}
        >
            <div className={styles.contentContainer}>
                <div className={styles.logo}>
                    <Logo />
                </div>
                <div className={styles.heading}>
                    <h1>
                        {process.env.TRIAL
                            ? t('ConfirmSubscriptionPage.title_confirm')
                            : t('ConfirmSubscriptionPage.title')}
                    </h1>
                </div>
                <div className={styles.order}>
                    <ul className={styles.orderList}>
                        <li>
                            <span>{t('ConfirmSubscriptionPage.order.plan')}</span>
                            <span>{t(`ConfirmSubscriptionPage.order.${period}`, { plan: plan.name })}</span>
                        </li>
                        {isTrialAvailable ? (
                            <li
                                style={{ display: 'block' }}
                                dangerouslySetInnerHTML={{
                                    __html: t('ConfirmSubscriptionPage.order.charge_in_the_future', {
                                        days: process.env.TRIAL,
                                        chargedAt: moment()
                                            .add(process.env.TRIAL, 'd')
                                            .format('MMMM Do YYYY'),
                                        price: formatPrice(price),
                                    }),
                                }}
                            />
                        ) : (
                            <li>
                                <span>{t('ConfirmSubscriptionPage.order.total')}</span>
                                <span>{formatPrice(price)}</span>
                            </li>
                        )}
                    </ul>
                </div>
                <div className={styles.payForm}>
                    <Checkout planId={planId} period={period} inline />
                </div>
            </div>
        </SecondLevelScreen>
    );
}
