import PropTypes from 'prop-types';
import React from 'react';
import { Route, useRouteMatch } from 'react-router-dom';

import { AnimateableSwitch } from '@mssgme/ui';
import { useWebsiteData } from '@mssgme/userpage-core';
import { ProductsContextProvider, ProductsPage } from '@mssgme/products';
import { BlockEditContextProvider } from '../hooks';
import { ChooseBlock, ConvertServices, EDITOR_ROUTES, useEditorsPreload } from './Sheets';

const EditorsRouting = (props) => <AnimateableSwitch absolute routing={routing} {...props} />;

EditorsRouting.propTypes = {
    absolute: PropTypes.bool,
};

const ProductsRouting = () => {
    const { _id: websiteId } = useWebsiteData();
    const { path } = useRouteMatch();

    return (
        <ProductsContextProvider base={path} websiteId={websiteId}>
            <Route component={ProductsPage} />
        </ProductsContextProvider>
    );
};

const editorsRouting = Object.fromEntries(
    EDITOR_ROUTES.map(([kind, component]) => ([kind, { component, exact: false }]))
);

const routing = {
    products: ProductsRouting,
    'add-block': {
        component: ChooseBlock,
        exact: true,
        children: {
            ...editorsRouting,
            ':kind': EditorsRouting,
        },
    },
    'block': {
        children: {
            ...Object.fromEntries(
                Object.entries(editorsRouting).map(([key, route]) => [`${key}/:blockId`, route])
            ),
            ':kind/:blockId': {
                component: EditorsRouting,
                exact: false,
                children: {
                    convert: ConvertServices,
                },
            },
        },
    },
};

export const LandingsEditorsRouter = () => {
    const isReady = useEditorsPreload();

    if (!isReady) {
        return null;
    }

    return (
        <BlockEditContextProvider>
            <EditorsRouting absolute={false} />
        </BlockEditContextProvider>
    );
};
