import { Embed, InstagramShareNew, Qr, ShareNew, Socials, TikTokShare } from './Page';
import { WebsiteDomainEdit, WebsiteSlugEdit } from './Website';

export const SHARE_ROUTE_PATH = ':share(share|publish/published/share|share/share|standalone)';
export const BIO_ROUTE_PATH = ':socials(socials|bio)';
export const NAME_ROUTE_PATH = 'name';

export const bioShareRouting = {
    component: Socials,
    children: {
        instagram: InstagramShareNew,
        tiktok: TikTokShare,
    },
};

export const publishNameRouting = {
    component: WebsiteSlugEdit,
    children: {
        domain: WebsiteDomainEdit,
    },
};

export const shareRouting = {
    component: ShareNew,
    children: {
        embed: Embed,
        qr: Qr,
        [NAME_ROUTE_PATH]: publishNameRouting,
        [BIO_ROUTE_PATH]: bioShareRouting,
    },
};
