import PropTypes from 'prop-types';
import invariant from 'tiny-invariant';
import React, { useMemo } from 'react';
import { matchPath, Route, useLocation, useRouteMatch } from 'react-router-dom';
import { AnimatePresence } from 'framer-motion';
import { flatten } from 'lodash';

import { RoutingTypes } from '../domainTypes';

const isComponent = (target) => (('$$typeof' in target) || (typeof target === 'function'));
const join = (parent, child) => ((parent && child) ? `${parent}/${child}` : child).replace(/\/{2,}/g, '/');

const mapChildRoute = function *(path, routeConfig) {
    invariant(routeConfig, `Miss-configured route "${path}"`);

    const { component, exact, strict, sensitive, children, props } = isComponent(routeConfig)
        ? { component: routeConfig }
        : routeConfig;

    if (component) {
        yield {
            exact: exact ?? !children,
            path,
            component,
            strict,
            sensitive,
            props,
        };
    }

    if (children) {
        for (const [childPath, config] of Object.entries(children)) {
            yield * mapChildRoute(join(path, childPath), config);
        }
    }
};

const mapRouting = (path, children) =>
    flatten(
        Array.from(mapChildRoute(path, { children }))
    );

/**
 * ! When nesting recursively same routing, it must be declared with `absolute` or specific `base` to
 * avoid infinite recursion
 *
 * @param {object} props Props
 * @param {Record.<string, ComponentType|{ component?: ComponentType; exact?: boolean; children?: * }>} props.routing Routes
 * @param {string} [props.base] Optional base path if `absolute` is `false`. If `absolute` is `false` and no
 * `base` specified - current matched route path would be used
 * @param {boolean} [props.absolute] Use `location.pathname` as base path if `true`
 * @returns {ReactNode} React node
 */
export const AnimateableSwitch = ({ routing, base, absolute }) => {
    const { path } = useRouteMatch();
    const { pathname } = useLocation();
    // when recursively nesting routers, remember this node depth
    const nested = useMemo(() => pathname.split('/').length, [path]);
    // throw-away rest of the location if matching upper node of nested routing
    const baseline = useMemo(() => pathname.split('/').slice(0, nested).join('/'), [nested, pathname]);
    const basePath = useMemo(() => (absolute ? baseline : (base || path)), [baseline, absolute, base || path]);
    const resolved = useMemo(() => mapRouting(basePath, routing), [basePath, routing]);

    return (
        <Route path={basePath}>
            <AnimatePresence>
                {resolved.map((route) => {
                    const matched = matchPath(pathname, route);
                    const Component = matched && route.component;

                    return Component && (
                        <Component key={matched.path} {...matched.params} {...route.props} />
                    );
                })}
            </AnimatePresence>
        </Route>
    );
};

AnimateableSwitch.propTypes = {
    base: PropTypes.string,
    absolute: PropTypes.bool,
    routing: RoutingTypes.children.isRequired,
};
