import React from 'react';

import { AnimateableSwitch } from '@mssgme/ui';
import { GuideBuilder, PageSetup } from './Page';
import { Help, SortHelp } from './Sheets';
import { SETTINGS_ROUTE_PATH, websiteSettingsRouting } from './WebsiteSettingsRouting';
import { SHARE_ROUTE_PATH, shareRouting } from './ShareRouting';
import { PAGES_ROUTE_PATH, pagesRouting } from './PagesRouting';
import { PAGE_STYLING_ROUTE_PATH, pageStylingRouting } from './PageStylingRouting';
import { PUBLISH_ROUTE_PATH, publishRouting } from './PublishRouting';

const routing = {
    setup: PageSetup,
    sort: SortHelp,
    help: Help,
    guide: GuideBuilder,
    [PAGES_ROUTE_PATH]: pagesRouting,
    [SETTINGS_ROUTE_PATH]: websiteSettingsRouting,
    [PAGE_STYLING_ROUTE_PATH]: pageStylingRouting,
    [PUBLISH_ROUTE_PATH]: publishRouting,
    [SHARE_ROUTE_PATH]: shareRouting,
};

export const LandingsBuilderRouter = () => <AnimateableSwitch routing={routing} />;
