import { useSelector } from 'react-redux';

export default function usePlanDetails(planId) {
    return useSelector(({ billingDetails: { plans, result } }) => {
        const isCurrentPlan = result?.subscription?.plan?.id === planId;
        const subscription = result?.subscription;

        return {
            plan: plans?.find((plan) => plan.id === planId),
            subscription,
            isCurrentPlan,
            currentPlanPeriod: subscription?.period,
        };
    });
}
