import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useToggle } from 'react-use';
import { Link } from 'react-router-dom';

import { useActions } from '@mssgme/hooks';
import { ImageField, ListInput, PortalModalSheet, TextField } from '@mssgme/ui';
import { AccountEditForm } from '../AccountEditForm';

const languages = {
    ru: '',
    en: 'English',
    uk: '',
};

export function AccountPage({ backPath }) {
    const { t } = useTranslation();
    const { api } = useActions();
    const [isLangChoose, setIsLangChoose] = useToggle(false);

    return (
        <AccountEditForm title={t('accountPage.heading')} backPath={backPath}>
            {({ values, setFieldValue }) => (
                <>
                    <ImageField
                        name="avatar"
                        gallery={false}
                        innerLabel={t('accountForm.label.avatar')}
                        previewUrl={values.avatarUrl}
                        upload={({ payload }) => api.users.uploadAvatar({ payload, field: 'avatar' })}
                        onChange={({ path, url }) => {
                            if (path) {
                                setFieldValue('avatar', path);
                                setFieldValue('avatarUrl', url);
                            }
                        }}
                    />
                    <TextField
                        builderInput
                        name="name"
                        label={t('accountForm.label.name')}
                        placeholder={t('accountForm.placeholder.name')}
                    />
                    <TextField
                        builderInput
                        name="email"
                        label={t('accountForm.label.email')}
                        placeholder={t('accountForm.placeholder.email')}
                    />
                    <div style={{ marginBottom: 12 }}>
                        <div
                            style={{
                                fontWeight: 500,
                                fontSize: 14,
                                lineHeight: '20px',
                                paddingLeft: 10,
                                marginBottom: 4,
                            }}
                        >
                            {t('accountForm.label.language')}
                        </div>
                        <div
                            onClick={setIsLangChoose}
                            style={{
                                height: 38,
                                borderRadius: 10,
                                backgroundColor: '#f5f5f5',
                                display: 'flex',
                                alignItems: 'center',
                                padding: '0 16px',
                                cursor: 'pointer',
                            }}
                        >
                            {languages[values.lang]}
                            <span style={{ marginLeft: 'auto', fontSize: 14, color: '#007aff' }}>
                                {t('accountForm.button.change')}
                            </span>
                        </div>
                    </div>
                    <Link to="/s/account/change-password" style={{ marginBottom: 12, display: 'block', color: '#333' }}>
                        <div
                            style={{
                                fontWeight: 500,
                                fontSize: 14,
                                lineHeight: '20px',
                                paddingLeft: 10,
                                marginBottom: 4,
                            }}
                        >
                            {t('accountForm.button.password')}
                        </div>
                        <div
                            style={{
                                height: 38,
                                borderRadius: 10,
                                backgroundColor: '#f5f5f5',
                                display: 'flex',
                                alignItems: 'center',
                                padding: '0 16px',
                                cursor: 'pointer',
                            }}
                        >
                            &bull;&bull;&bull;&bull;&bull;&bull;&bull;&bull;&bull;&bull;
                            <span style={{ marginLeft: 'auto', fontSize: 14, color: '#007aff' }}>
                                {t('accountForm.button.change')}
                            </span>
                        </div>
                    </Link>

                    {isLangChoose && (
                        <PortalModalSheet
                            title={t('accountForm.label.chooseLang')}
                            onClose={(lang) => {
                                setIsLangChoose(false);
                                lang && setFieldValue('lang', lang);
                            }}
                            render={({ handleClose }) => (
                                <ListInput
                                    value={values.lang}
                                    options={[
                                        {
                                            label: '',
                                            value: 'ru',
                                        },
                                        {
                                            label: '',
                                            value: 'uk',
                                        },
                                        {
                                            label: 'English',
                                            value: 'en',
                                        },
                                    ]}
                                    onChange={handleClose}
                                />
                            )}
                        />
                    )}
                </>
            )}
        </AccountEditForm>
    );
}

AccountPage.propTypes = {
    backPath: PropTypes.string.isRequired,
};
