import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import {
    FiUser,
    FiLogOut,
    FiTablet,
    FiDollarSign,
    FiHelpCircle,
    FiStar,
    FiCheck,
    FiShoppingBag,
    FiBell,
} from 'react-icons/fi';
import { RiArrowGoBackLine } from 'react-icons/ri';
import { CgTemplate } from 'react-icons/cg';

import {
    useNavigationUtils,
    useExperiments,
    useSwitchApp,
    useActions,
    useAccessControl,
    useUpgradeLocation,
    useFeature,
    useUiFlags,
} from '@mssgme/hooks';
import { ModalPane, BlockListItemPreview } from '@mssgme/ui';
import { InstallLink } from '../../../UI';

export function SettingsMenu({ backPath }) {
    const { t } = useTranslation();
    const { ui } = useActions();
    const { generateUrl } = useNavigationUtils();
    const { isOldVersionDisplayed } = useExperiments();
    const { isAdmin } = useAccessControl();
    const { isAdvancedStylingMode } = useUiFlags();
    const upgradeUrl = useUpgradeLocation({ preventDynamicBack: true, reason: false });
    const switchApp = useSwitchApp();
    const templatesEnabled = useFeature('templates');
    const advancedEnabled = useFeature('advanced_styling');

    const handleAdvancedMode = useCallback((e) => {
        e.preventDefault();
        ui.toggleStylingMode();
    });

    const onSwitch = useCallback((e) => {
        e.preventDefault();
        void switchApp('inbox');
    });

    return (
        <ModalPane title={t('settingsPage.heading')} onClose={backPath}>
            <BlockListItemPreview.List>
                {/*    <BlockListItemPreview*/}
                {/*        tag={Link}*/}
                {/*        to={generateUrl('/pages')}*/}
                {/*        labelIcon={FiLayout}*/}
                {/*        label={t('mainMenu.landings')}*/}
                {/*    />*/}
                {/*    <BlockListItemPreview*/}
                {/*        tag="a"*/}
                {/*        href={`${admin_url}/i`}*/}
                {/*        labelIcon={FiInbox}*/}
                {/*        label={t('mainMenu.inbox')}*/}
                {/*    />*/}
                <BlockListItemPreview
                    tag={Link}
                    to={generateUrl('account')}
                    labelIcon={FiUser}
                    label={t('settingsMenu.generalMenu.account')}
                />
                <BlockListItemPreview
                    tag={Link}
                    to={generateUrl('notifications')}
                    labelIcon={FiBell}
                    label={t('settingsMenu.generalMenu.notifications')}
                />
                {/*    <BlockListItemPreview*/}
                {/*        tag={Link}*/}
                {/*        to={generateUrl('/products')}*/}
                {/*        labelIcon={FiShoppingBag}*/}
                {/*        label={t('settingsMenu.generalMenu.products')}*/}
                {/*    />*/}
                {/*    <BlockListItemPreview*/}
                {/*        tag={Link}*/}
                {/*        to={generateUrl('/bots')}*/}
                {/*        labelIcon={CgBot}*/}
                {/*        label="Bots"*/}
                {/*    />*/}
                {templatesEnabled && (
                    <BlockListItemPreview
                        tag={Link}
                        to={generateUrl('/pages/templates')}
                        labelIcon={CgTemplate}
                        label={t('settingsMenu.generalMenu.templates')}
                    />
                )}
                {/*    <BlockListItemPreview*/}
                {/*        tag={Link}*/}
                {/*        to={generateUrl('invites')}*/}
                {/*        labelIcon={FiUsers}*/}
                {/*        label={t('settingsMenu.generalMenu.invites')}*/}
                {/*    />*/}
                {isAdmin && (
                    <BlockListItemPreview
                        tag={Link}
                        to={upgradeUrl}
                        labelIcon={FiDollarSign}
                        label={t('settingsMenu.generalMenu.billing')}
                    />
                )}
                {advancedEnabled && (
                    <BlockListItemPreview
                        tag="a"
                        href="#"
                        labelIcon={FiStar}
                        label={t('settingsMenu.generalMenu.advanced')}
                        rightIcon={isAdvancedStylingMode && <FiCheck />}
                        onClick={handleAdvancedMode}
                    />
                )}
                {process.env.REACT_APP_TARGET !== 'mobile' && (
                    <BlockListItemPreview
                        tag={InstallLink}
                        labelIcon={FiTablet}
                        label={t('settingsMenu.install')}
                    />
                )}
                <BlockListItemPreview
                    tag={Link}
                    to={generateUrl('support')}
                    labelIcon={FiHelpCircle}
                    label={t('settingsMenu.generalMenu.support')}
                />
                {isOldVersionDisplayed && (
                    <BlockListItemPreview
                        labelColor="#999"
                        iconColor="#999"
                        tag="a"
                        href="#"
                        onClick={onSwitch}
                        labelIcon={RiArrowGoBackLine}
                        label={t('mainMenu.oldVersion')}
                    />
                )}
                <BlockListItemPreview
                    tag={Link}
                    to="/logout"
                    labelIcon={FiLogOut}
                    label={t('settingsMenu.logout')}
                />
            </BlockListItemPreview.List>
        </ModalPane>
    );
}

SettingsMenu.propTypes = {
    backPath: PropTypes.string.isRequired,
};
