import { useCallback, useMemo, useState } from 'react';

import { useNavigationGuard } from '../components';

export function useBackTrack(rawBackTrack, rawRedirects) {
    const [redirectTo, setRedirectTo] = useState();
    const [backTrack, redirects] = useMemo(
        () => (rawBackTrack && Array.isArray(rawBackTrack) ? rawBackTrack : [rawBackTrack, rawRedirects]),
        [rawBackTrack, rawRedirects]
    );

    const checkRoute = useCallback(
        ({ pathname }) => {
            if (!backTrack) {
                return;
            }

            if (pathname.startsWith(`/${backTrack.replace(/^\//, '')}`)) {
                return;
            } else {
                setRedirectTo(redirects[backTrack]);
            }

            return true;
        },
        [redirects, backTrack, redirectTo]
    );

    useNavigationGuard({ blocked: !!backTrack && !redirectTo, forbid: checkRoute });

    return {
        redirectTo,
    };
}
