import {
    HasAdvancedBlocksMessage,
    NoBlocksMessage,
    NotPublishedMessage,
    PublishedMessage,
    SharePublished,
} from './Page';
import { BIO_ROUTE_PATH, bioShareRouting, NAME_ROUTE_PATH, publishNameRouting } from './ShareRouting';

export const PUBLISH_ROUTE_PATH = 'publish';

export const publishRouting = {
    children: {
        success: PublishedMessage,
        published: {
            component: SharePublished,
            exact: false,
            children: {
                [BIO_ROUTE_PATH]: bioShareRouting,
                [NAME_ROUTE_PATH]: publishNameRouting,
            },
        },
        'not-published': NotPublishedMessage,
        'no-blocks': NoBlocksMessage,
        'has-advanced-blocks': HasAdvancedBlocksMessage,
    },
};
