import { WEBSITE_DEFAULT_NAME, HOMEPAGE_DEFAULT_NAME } from '../constants';
import { __t } from '../trans';

const getHomePageName = (language) => HOMEPAGE_DEFAULT_NAME[language || 'en'];

export default (page, isHomePage, language = typeof navigator !== 'undefined' && navigator.language || 'en') => {
    const name = page.name;

    if (isHomePage) {
        if (!name) {
            return getHomePageName(language);
        }

        for (const [lang, title] of Object.entries(WEBSITE_DEFAULT_NAME)) {
            if (name === title) {
                return getHomePageName(lang);
            }
        }
    }

    return name;
};
