import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { Link, Redirect } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { Button, NotificationCenter } from '@mssgme/ui';

import styles from './SomethingWrong.scss';

export default function SomethingWrong({ error }) {
    const { t } = useTranslation('common');

    useEffect(() => {
        if (error.name !== 'AuthorizationError') return;

        const center = NotificationCenter.getInstance();
        const id = center.danger(t('errorsList.unauthorized'), { showClose: true });

        return () => {
            center.remove(id);
        };
    }, [error]);

    if (error.name === 'AuthorizationError') {
        return (
            <Redirect to="/logout" />
        );
    }

    if (error.name === 'NetworkError') {
        return (
            <div className={styles.root}>
                <div className={styles.content}>
                    <h2 className={styles.heading}>
                        {t('SomethingWrong.networkError')}
                    </h2>
                    <p>
                        <Button btnStyle="danger" onClick={() => window.location.reload()} fullWidth>
                            {t('SomethingWrong.reload')}
                        </Button>
                    </p>
                </div>
            </div>
        );
    }

    return (
        <div className={styles.root}>
            <div className={styles.content}>
                <h2 className={styles.heading}>
                    {t('SomethingWrong.error')}
                </h2>
                <code className={styles.code}>
                    {error.message}
                </code>
                <p>
                    <Button fullWidth btnStyle="danger" tag="a" href="/logout">
                        {t('SomethingWrong.logout')}
                    </Button>
                </p>
            </div>
        </div>
    );
}

SomethingWrong.propTypes = {
    error: PropTypes.object.isRequired,
};
