import React from 'react';
import { useTranslation } from 'react-i18next';
import { Redirect } from 'react-router-dom';

import { __t } from '@mssgme/shared';
import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { Nav, NavItemLink, PersonalLinkNav, RestrictionScope, SecondLevelScreen } from '@mssgme/ui';
import { Edit2Icon, SettingsIcon } from '@mssgme/icons';
import { usePageUrl } from '../../../../hooks';
import { CopyableWebsiteLink, PublishButton } from '../../../../UI';

import styles from './PageHome.scss';

export function PageHome() {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const pageUrl = usePageUrl(website, page);
    const isPublished = page.enabled;
    const isHomePage = page._id === website.homePageId;

    if (!page) {
        return <Redirect to={generateUrl(`/pages/-/${website._id}`)}/>;
    }

    const topContent = (
        <>
            <CopyableWebsiteLink
                url={pageUrl}
                website={website}
                page={page}
            />
            {isPublished && (
                <PersonalLinkNav
                    className={styles.personalLink}
                    // embedTo={generateUrl('share/embed')}
                    sharePageTo={generateUrl('share')}
                    qrPageTo={{ pathname: generateUrl('standalone/qr'), state: { backPath: '../..' } }}
                    textToCopy={pageUrl}
                    website={website}
                />
            )}
        </>
    );

    return (
        <SecondLevelScreen
            title={isHomePage ? __t('homePage') : page.name}
            backPath={generateUrl('../list')}
            titleRightContent={
                <PublishButton
                    className={styles.publishButton}
                    btnStyle="primary"
                    website={website}
                    page={page}
                    publishedUrl="publish/published"
                    data-button="publish"
                >
                    {t('Editor.Dashboard.header.publish')}
                </PublishButton>
            }
            topPanelChildren={topContent}
        >
            <RestrictionScope>
                <Nav>
                    <NavItemLink to={generateUrl('edit')} icon={Edit2Icon}>
                        {t('Website.View.Home.items.editor')}
                    </NavItemLink>
                    <NavItemLink to={generateUrl('settings')} icon={SettingsIcon} disabled={isHomePage}>
                        {t('Website.View.Home.items.settings')}
                    </NavItemLink>
                </Nav>
            </RestrictionScope>
        </SecondLevelScreen>
    );
}
