import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { matchPath, Redirect, useLocation } from 'react-router-dom';

import { AnimateableSwitch } from '@mssgme/ui';
import { useBackProductsPath, useProductsContext } from '../hooks';
import {
    CollectionEdit,
    CollectionProducts,
    Collections,
    PaymentProviderSettingsScreen,
    ProductEdit,
    ProductsContacts,
    ProductsCurrency,
    ProductsHome,
    ProductsImport,
    ProductsNotifications,
    ProductsOnboarding,
    ProductsOrderForm,
} from './Editing';
import { Payments, ProductsSettingsMenu } from './SettingsMenu';

const routing = {
    '/': {
        component: ProductsHome,
        children: {
            'settings': {
                component: ProductsSettingsMenu,
                children: {
                    'onboard/:fromStart?': ProductsOnboarding,
                    'currency': ProductsCurrency,
                    'messengers': ProductsContacts,
                    'notifications': ProductsNotifications,
                    'form': ProductsOrderForm,
                    'import': ProductsImport,
                    'payments': {
                        component: Payments,
                        children: {
                            ':provider': PaymentProviderSettingsScreen,
                        },
                    },
                },
            },
            'collections': {
                component: Collections,
                children: {
                    'new': CollectionEdit,
                    ':collectionId([a-f\\d]{24})': {
                        component: CollectionProducts,
                        children: {
                            new: ProductEdit,
                            edit: CollectionEdit,
                            'product/edit/:productId': ProductEdit,
                        },
                    },
                },
            },
            'new/:collectionId?': ProductEdit,
            'edit/:productId': ProductEdit,
        },
    },
};

export function ProductsPage() {
    const { router, crm, modal } = useProductsContext();
    const { pathname, state } = useLocation();
    const backUrl = useMemo(() => state?.blockUrl, []);
    const isOnboarding = !!matchPath(pathname, router.settings.toOnboarding());

    useBackProductsPath(backUrl);

    if (crm.onBoarding && !isOnboarding) {
        return <Redirect to={router.settings.toOnboarding()} />;
    }

    return <AnimateableSwitch modal={modal} routing={routing} base={router.toHome()} />;
}

ProductsPage.propTypes = {
    defaultMessengers: PropTypes.any,
};
